<template>
  <div class="box">
    <!-- <dialog-bar v-model="sendVal" type="danger" title="ÊàëÊòØÊ†áÈ¢ò" content="ÊàëÊòØÂÜÖÂÆπ" v-on:cancel="clickCancel()" @danger="clickDanger()" @confirm="clickConfirm()" dangerText="Delete"></dialog-bar> -->
    <div class="header">
      <span class="header-left">ÂæÖÂäû‰∫ãÈ°π</span>
      <span class="header-right"
        ><a href="/"><i class="el-icon-position"></i>ÂØºËà™È°µ</a>
        <a href="/logout"><i class="el-icon-switch-button"></i>ÈÄÄÂá∫</a></span
      >
    </div>

    <el-input
      placeholder="ËØïËØïÁî® #ÂÜÖÂÆπ# ÁöÑÊ†ºÂºèÊ∑ªÂä†Ê†áÁ≠æÔºå‰æãÂ¶ÇÔºö#Âä†Ê≤πüí™#"
      v-model="input"
      clearable
    ></el-input>
    <div class="btnbox">
      <el-button
        class="elbtn"
        type="success"
        :disabled="this.input.length === 0"
        size="small"
        icon="el-icon-thumb"
        @click="submit"
        >Ê∑ªÂä†Êñ∞‰ªªÂä°</el-button
      >
    </div>

    <el-tabs type="border-card">
      <el-tab-pane label="ÂÖ®ÈÉ®">
        <h3 v-if="activeTasks.length === 0">Êó†ËÆ∞ÂΩï...</h3>
        <span v-else>ÂÖ±{{ activeTasks.length }}Êù°ËÆ∞ÂΩï</span>
        <Card
          v-for="item in activeTasks"
          :key="item.id"
          :id="item.id"
          :task="item.task"
          :date="item.date"
          :time="item.time"
          :editstatus="item.editstatus"
          :done="item.done"
          :tag="item.tag"
          @emitstatus="getstatus"
          @emitremove="getremove"
          @emitedit="getedit"
        ></Card>
      </el-tab-pane>

      <el-tab-pane label="Êú™ÂÆåÊàê">
        <h3 v-if="unfinishedTasks.length <= 0">Êó†ËÆ∞ÂΩï...</h3>
        <span v-else>ÂÖ±{{ unfinishedTasks.length }}Êù°ËÆ∞ÂΩï</span>
        <!-- <TodoTab v-if="unfinishedThings.length > 0" @sendidtohome="getidfromtab" :things="unfinishedThings"></TodoTab> -->
        <Card
          v-for="item in unfinishedTasks"
          :key="item.id"
          :id="item.id"
          :task="item.task"
          :date="item.date"
          :time="item.time"
          :editstatus="item.editstatus"
          :done="item.done"
          :tag="item.tag"
          @emitstatus="getstatus"
          @emitremove="getremove"
          @emitedit="getedit"
        ></Card>
      </el-tab-pane>

      <el-tab-pane label="Â∑≤ÂÆåÊàê">
        <h3 v-if="finishedTasks.length <= 0">Êó†ËÆ∞ÂΩï...</h3>
        <span v-else>ÂÖ±{{ finishedTasks.length }}Êù°ËÆ∞ÂΩï</span>
        <!-- <TodoTab v-if="finishedThings.length > 0" @sendidtohome="getidfromtab" :things="finishedThings"></TodoTab> -->
        <Card
          v-for="item in finishedTasks"
          :key="item.id"
          :id="item.id"
          :task="item.task"
          :date="item.date"
          :time="item.time"
          :editstatus="item.editstatus"
          :done="item.done"
          :tag="item.tag"
          @emitstatus="getstatus"
          @emitremove="getremove"
          @emitedit="getedit"
        ></Card>
      </el-tab-pane>
      <el-tab-pane label="ÂõûÊî∂Á´ô">
        <span id="recyclebin"
          ><i>ÂõûÊî∂Á´ôÈáåÁöÑÊï∞ÊçÆÊúÄÂ§ö‰øùÁïô‰∏ÄÂπ¥(Êåâ366Â§©ËÆ°ÁÆó),ËøáÊúüËá™Âä®Âà†Èô§</i></span
        >
        <br />
        <h3 v-if="recyclebinTasks.length <= 0">ËøôÈáå‰∏ÄÁâáËôöÊó†...</h3>
        <span v-else>ÂÖ±{{ recyclebinTasks.length }}Êù°ËÆ∞ÂΩï</span>
        <RecyclebinCard
          v-for="item in recyclebinTasks"
          :key="item.id"
          :id="item.id"
          :task="item.task"
          :date="item.date"
          :editstatus="item.editstatus"
          :done="item.done"
          :tag="item.tag"
          :deletetime="item.deletetime"
          :lefttime="item.lefttime"
          @emitrecover="getrecover"
          @emitdelete="getdelete"
        ></RecyclebinCard>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
// import "bootstrap/dist/css/bootstrap.min.css";
// import "bootstrap/dist/js/bootstrap.min.js";
import Card from "./Card";
import RecyclebinCard from "./RecyclebinCard";
import axios from "axios";
export default {
  components: {
    Card,
    RecyclebinCard,
  },
  data() {
    return {
      input: "",
      allTasks: [],
    };
  },
  computed: {
    activeTasks() {
      return this.allTasks.filter((e) => {
        return !e.deleted;
      });
    },
    finishedTasks() {
      return this.allTasks.filter((e) => {
        return !e.deleted && e.done;
      });
    },
    unfinishedTasks() {
      return this.allTasks.filter((e) => {
        return !e.deleted && !e.done;
      });
    },
    recyclebinTasks() {
      return this.allTasks.filter((e) => {
        return e.deleted;
      });
    },
  },
  created() {
    axios
      .post("/todolist/api", {
        code: 100,
        data: "",
      })
      .then((res) => {
        if (res.data.data.length > 0) {
          this.allTasks = res.data.data;
        }
      })
      .catch((error) => {
        console.log(error);
        this.$message({
          showClose: true,
          message: "ËØ∑Ê±ÇÊúçÂä°Âô®Êï∞ÊçÆÂá∫ÈîôÔºÅ",
          type: "error",
        });
      });
  },
  methods: {
    getstatus(id) {
      var pos = this.allTasks.findIndex((n) => n.id == id);
      var jsonData = {
        code: 101,
        data: {
          id: id,
          done: Math.pow(0, this.allTasks[pos].done),
        },
      };
      axios
        .post("/todolist/api", jsonData)
        .then((res) => {
          this.allTasks[pos].done = !this.allTasks[pos].done;
        })
        .catch((error) => {
          console.log(error);
          this.$message({
            showClose: true,
            message: "ÂêëÊúçÂä°Âô®ËØ∑Ê±ÇÊõ¥Êñ∞Êï∞ÊçÆÂá∫ÈîôÔºÅ",
            type: "error",
          });
        });
    },
    getremove(id) {
      var pos = this.allTasks.findIndex((n) => n.id == id);
      var jsonData = {
        code: 103,
        data: id,
      };
      this.$confirm("Á®çÂêé‰Ω†ÂèØ‰ª•Âú®ÂõûÊî∂Á´ô‰∏≠ÊâæÂà∞ÂÆÉ, ÁªßÁª≠Âêó?", "ÁßªÈô§ËØ•ËÆ∞ÂΩï", {
        confirmButtonText: "Á°ÆÂÆö",
        cancelButtonText: "ÂèñÊ∂à",
        type: "warning",
      })
        .then(() => {
          axios
            .post("/todolist/api", jsonData)
            .then((res) => {
              this.$message({
                type: "success",
                message: "ÁßªÈô§ÊàêÂäü!",
              });
              this.allTasks[pos].deleted = 1;
            })
            .catch((error) => {
              console.log(error);
              this.$message({
                showClose: true,
                message: "ÂêëÊúçÂä°Âô®ËØ∑Ê±ÇÊõ¥Êñ∞Êï∞ÊçÆÂá∫ÈîôÔºÅ",
                type: "error",
              });
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "Â∑≤ÂèñÊ∂àÁßªÈô§",
          });
        });
    },
    getedit(id) {
      var pos = this.allTasks.findIndex((n) => n.id == id);
      this.$prompt("‰Ω†‰πüÂèØ‰ª•ËæìÂÖ•üòçemojiË°®ÊÉÖÂì¶", "ÁºñËæëÂÜÖÂÆπ", {
        confirmButtonText: "Á°ÆÂÆö",
        cancelButtonText: "ÂèñÊ∂à",
        inputValue: this.allTasks[pos].task + this.allTasks[pos].tag,
      })
        .then(({ value }) => {
          var jsonData = {
            code: 102,
            data: {
              id: id,
              content: value,
            },
          };
          axios
            .post("/todolist/api", jsonData)
            .then((res) => {
              this.allTasks[pos].task = res.data.data.task;
              this.allTasks[pos].tag = res.data.data.tag;
              this.$message({
                type: "success",
                message: "‰øÆÊîπÂÜÖÂÆπÊèê‰∫§ÊàêÂäüÔºÅ",
              });
            })
            .catch((error) => {
              console.log(error);
              this.$message({
                showClose: true,
                message: "ÂêëÊúçÂä°Âô®ËØ∑Ê±ÇÊõ¥Êñ∞Êï∞ÊçÆÂá∫ÈîôÔºÅ",
                type: "error",
              });
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "ÂèñÊ∂àÁºñËæë",
          });
        });
    },
    getrecover(id) {
      var pos = this.allTasks.findIndex((n) => n.id == id);
      var jsonData = {
        code: 104,
        data: id,
      };
      this.$confirm("Â∞ÜÊ≠§ËÆ∞ÂΩïÊÅ¢Â§çÂà∞‰ª•ÂâçÁöÑ‰ΩçÁΩÆ, ÁªßÁª≠Âêó?", "ÊÅ¢Â§ç", {
        confirmButtonText: "Á°ÆÂÆö",
        cancelButtonText: "ÂèñÊ∂à",
        type: "warning",
      })
        .then(() => {
          axios
            .post("/todolist/api", jsonData)
            .then((res) => {
              this.$message({
                type: "success",
                message: "ÊÅ¢Â§çÊàêÂäü!",
              });
              this.allTasks[pos].deleted = 0;
            })
            .catch((error) => {
              console.log(error);
              this.$message({
                showClose: true,
                message: "ÂêëÊúçÂä°Âô®ËØ∑Ê±ÇÊõ¥Êñ∞Êï∞ÊçÆÂá∫ÈîôÔºÅ",
                type: "error",
              });
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "Â∑≤ÂèñÊ∂àÊÅ¢Â§ç",
          });
        });
    },
    getdelete(id) {
      var pos = this.allTasks.findIndex((n) => n.id == id);
      var jsonData = {
        code: 105,
        data: id,
      };
      console.log("delete");
      this.$confirm("Ê≠§Êìç‰ΩúÂ∞ÜÊ∞∏‰πÖÂà†Èô§ËØ•ËÆ∞ÂΩï, ÊòØÂê¶ÁªßÁª≠?", "ÊèêÁ§∫", {
        confirmButtonText: "Á°ÆÂÆö",
        cancelButtonText: "ÂèñÊ∂à",
        type: "warning",
        center: true,
      })
        .then(() => {
          axios
            .post("/todolist/api", jsonData)
            .then((res) => {
              this.allTasks.splice(pos, 1);
              this.$message({
                type: "success",
                message: "Âà†Èô§ÊàêÂäü!",
              });
            })
            .catch((error) => {
              this.$message({
                showClose: true,
                message: "ÂêëÊúçÂä°Âô®ËØ∑Ê±ÇÊõ¥Êñ∞Êï∞ÊçÆÂá∫ÈîôÔºÅ",
                type: "error",
              });
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "Â∑≤ÂèñÊ∂àÂà†Èô§",
          });
        });
    },
    submit() {
      var intext = this.input.trim();
      if (intext.length <= 0) {
        this.input = "";
        return;
      }
      var jsonData = {
        code: 106,
        data: intext,
      };
      axios
        .post("/todolist/api", jsonData)
        .then((res) => {
          this.allTasks.push(res.data.data);
          this.input = "";
        })
        .catch((error) => {
          console.log(error);
          this.$message({
            showClose: true,
            message: "ËØ∑Ê±ÇÊúçÂä°Âô®Êï∞ÊçÆÂá∫ÈîôÔºÅ",
            type: "error",
          });
        });
    },
  },
};
</script>

<style scoped>
.header {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  margin-bottom: 10px;
}
.header-left {
  font-size: 30px;
  padding-left: 20px;
}
.header-right {
  padding-right: 15px;
}

.header-right a {
  /* text-decoration: none; */
  color: black;
}

.btnbox {
  text-align: right;
}
.elbtn {
  margin: 5px;
  margin-right: 5px;
}

.box {
  max-width: 600px;
  margin: 0 auto;
}

#recyclebin {
  font-size: 15px;
  color: rgb(245, 44, 18);
}
</style>


